import React from 'react'
import {graphql} from 'gatsby'
import Layout from '../components/layout'
import {Helmet} from 'react-helmet'
import {Flex, Box} from '@rebass/grid/'

export default ({data}) => {
  const post = data.markdownRemark
  return (
    <Layout>
      <Flex pt={5}>
        <Helmet>
          <meta charSet="utf-8" />
          <title>{data.site.siteMetadata.title} | Contact</title>
          <link
            rel=""
            target="_blank"
            href="http://twitter.com/positivneutron"
          />
        </Helmet>
        <div style={{width: '100%'}}>
          <h1>{post.frontmatter.title}</h1>
          <div
            dangerouslySetInnerHTML={{__html: post.html}}
            style={{lineHeight: '2em'}}
          />
          <JustComments />
        </div>
      </Flex>
    </Layout>
  )
}

class JustComments extends React.Component {
  constructor(...args) {
    super(...args)
    this.ref = React.createRef()
  }
  render() {
    return (
      <div
        ref={this.ref}
        className="just-comments"
        data-apikey="405ee19e-1f3b-45a7-a2c5-0557cf43a817"
      />
    )
  }
  componentDidMount() {
    const s = document.createElement('script')
    s.src = '//just-comments.com/w.js'
    s.setAttribute('data-timestamp', +new Date())
    this.ref.current.appendChild(s)
  }
}

export const query = graphql`
  query($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: {slug: {eq: $slug}}) {
      html
      frontmatter {
        title
      }
    }
  }
`
